:root {
  --basic-black: #000000;
  --basic-grey: #333333;
  --basic-blue: #81b3d2;
  --basic-white: #ffffff;
  --background-grey: #eeeeee;
  --basic-brown: #766357;
  --basic-brown-hover: #604e43;
  --push-brown: #503e33;
  --form-chill: #f2f2f2;
  --form-push: #ebebeb;
  --calendar-chill: #a9a9a9;
  --extra-blue: #669ec0;
  --dark-blue: #5496bd;
  --basic-grey: #bdbbbc;
  --icons-of-dir-chill: #cacaca;
  --disabled: #6a6a6a;
  --date-of-check-in-focus: #e5e5e5;
  --background-toggle: #ababab;
  --box-shadow-toggle: #cfcfcf;
  --rate-wrapper: #666666;
  --icon-grey: #E6E6E6;
}
a {
  text-decoration: none;
  cursor: pointer;
}
ul {
  list-style: none;
}
body {
  font-family: "PT Sans", Arial, sans-serif;
  font-style: normal;
  font-size: 18px;
  min-width: 320px;
  text-transform: uppercase;
  margin: 0 auto;
  max-width: 1280px;
}
@media (max-width: 1280px) {
  body {
    margin: 0 auto;
  }
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2);
}
.page-header {
