.main-nav__toggle {
  position: absolute;
  top: 33px;
  right: 1px;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: inherit;
  cursor: pointer;
  border: none;
  font-size: 0;
  fill: var(--basic-blue);
}
.main-nav__toggle:focus,
.main-nav__toggle:hover {
  outline: none;
}
.main-nav__toggle:active {
  outline: none;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before {
  content: "";
  position: absolute;
  bottom: 62px;
  right: 18px;
  width: 23px;
  height: 4px;
  background-color: var(--basic-blue);
  box-shadow: 0 8px 0 0 var(--basic-blue), 0 16px 0 0 var(--basic-blue);
}
.main-nav__toggle:hover::before,
.main-nav__toggle:focus::before {
  opacity: 0.6;
  outline: none;
}
.main-nav__toggle:active::before {
  opacity: 0.3;
  outline: none;
}
.header-item-right {
  text-align: right;
}
@media (min-width: 768px) {
  .main-nav {
    display: grid;
    grid-template-columns: minmax(135px, 0.5fr) minmax(min-content, 1fr) minmax(min-content, 1fr) minmax(149px, 0.5fr);
    padding-top: 15px;
    padding-bottom: 15px;
    grid-gap: 0px;
    padding-left: 47px;
    padding-right: 26px;
    text-align: left;
  }
}
@media (min-width: 1280px) {
