    line-height: 26px;
    color: var(--basic-black);
    font-weight: bold;
    padding-left: 90px;
    padding-right: 72px;
  }
}
@media (max-width: 767px) {
  .main-nav .site-list__item {
    display: grid;
    color: var(--basic-white);
    background-color: var(--basic-blue);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    mix-blend-mode: normal;
    place-items: center;
    line-height: 40px;
    font-weight: bold;
    box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 1px 0px rgba(255, 255, 255, 0.2);
    text-align: center;
    padding-top: 15px;
    padding-bottom: 10px;
  }
  .main-nav .site-list__item:focus,
  .main-nav .site-list__item:hover {
    background-color: var(--extra-blue);
  }
  .main-nav .site-list__item:active {
