.radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.radio + label {
  position: relative;
  padding-left: 75px;
  padding-right: 5px;
  padding-top: 15px;
  margin-bottom: 30px;
  cursor: pointer;
  text-transform: none;
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  white-space: nowrap;
}
@media (min-width: 768px) {
  .radio + label {
    padding-top: 13px;
    padding-left: 75px;
    padding-right: 30px;
    padding-bottom: 10px;
    margin-top: 0;
    margin-bottom: 30px;
  }
}
@media (min-width: 1280px) {
  .radio + label {
    padding-top: 16px;
    padding-left: 77px;
    padding-right: 30px;
    padding-bottom: 7px;
    margin-top: 0;
    margin-bottom: 30px;
  }
}
.radio + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 50px;
  height: 50px;
  border: 5px solid var(--form-chill);
  border-radius: 50%;
  background: var(--form-chill);
}
.radio + label:hover::before,
.radio + label:focus::before {
  border: 5px solid var(--date-of-check-in-focus);
  background: var(--date-of-check-in-focus);
}
.radio + label:active::before {
  background: #D5D5D5;
  mix-blend-mode: normal;
  border: 5px solid #D5D5D5;
}
.radio + label:after {
  content: '';
  position: absolute;
  top: 20px;
  left: 20px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: var(--basic-black);
  opacity: 0;
  transition: 0.2s;
  border: 3px solid var(--basic-black);
}
.radio:checked + label:after {
  opacity: 1;
}
