.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}
.checkbox + label {
  position: relative;
  padding: 40px 30px 44px 75px;
  cursor: pointer;
  width: 100%;
  text-transform: none;
  padding-top: 10px;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
}
@media (min-width: 768px) {
}
@media (min-width: 1280px) {
  .checkbox + label {
    padding: 14px 10px 0px 75px;
  }
}
.checkbox + label:before {
  content: '';
  position: absolute;
  top: 0px;
  left: 0;
  width: 55px;
  height: 55px;
  border-radius: 5px;
  background: var(--form-chill);
  transition: 0.2s;
}
.checkbox + label:hover::before,
.checkbox + label:focus::before {
  background: var(--date-of-check-in-focus);
}
.checkbox + label:active::before {
  background: #D5D5D5;
  mix-blend-mode: normal;
}
.checkbox + label::after {
  content: '';
  position: absolute;
  top: 16px;
  left: 15px;
  width: 55px;
  height: 55px;
  transition: 0.2s;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  opacity: 0;
}
.checkbox + label:active::before {
  background: #333333;
  mix-blend-mode: normal;
  opacity: 0.3;
}
.checkbox:checked + label:after {
  opacity: 1;
}
